/*https://www.wix.com/website-template/view/html/2312?originUrl=https%3A%2F%2Fwww.wix.com%2Fwebsite%2Ftemplates%2Fhtml%2Fportfolio-cv%2Fpersonal%2F3&tpClick=view_button&esi=dcb5e32d-6d8a-4454-9633-822127b5dd04*/
/* #015761 
#f7dc4f*/

:root{
    --primary:#05445E;
    --second:#75E6DA;
    --third:#f9fafa;
    --fourth:#f1f0f0;
}
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.name-humbergur button,.image-icons,
.main-about-container .about-image,
.main-header-container .name-humbergur{
    display: none;
}
.main-header-container{
    display:grid;
    grid-template-columns: repeat(2,1fr);
    background:var(--primary);

}
.main-header-container .side-image img{ 
    width: 100%;
    height: 100vh;
}
.hey-name{
    background:var(--primary);
    margin: 25px 10px;
    height:30vh;
    margin-left: 50px;
}
.hey-name .head-name{
    font-size: 20px;
    font-weight: 500;  
    color:var(--second); 
}
.hey-name .links{
    margin-top:30px;
}
.hey-name .links a{
    padding-right:30px;
    text-decoration: none;
    color:var(--third);
    font-size: 14px;
}
.hey-name .hey-h1{
    font-size: 50px;
    font-weight: 500;  
    color:var(--second);
    margin-top:250px;
}
.hey-name .name-h1{
    font-size: 100px;
    padding:10px 0px;  
    color:var(--fourth);
}
.hey-name .hey-p{
    font-size: 14px;
    color:var(--third);
    margin-left: 15px;
}

.main-about-container{
    width:100%;
    background:var(--fourth);
}
.main-about-container .about-content{
    display: grid;
    justify-items: center;
    text-align: center;
    width:60%;
    margin:auto;
}
.about-content h1
{
    font-size: 40px;
    font-weight:600;
    padding:30px;  
    color:var(--primary);   
}
.about-content p{
    padding:10px;
    line-height: 30px;
    font-size: 14px;
}
.about-content button{
    width:20%;
    margin:auto;
    height:40px;
    margin:40px;
    border:none;
    outline: none;
    background:var(--second);
    color:var(--primary);
    margin-bottom: 170px;
}
.show-content{
    display: grid;
    justify-items: center;
}
.show-content h1,
.image-content h1{
    margin-bottom:60px;
    margin-top:60px;
    font-size: 70px;
    color:var(--primary); 
}
.show-image img{
    width: 100%;
    height: 800px;
    object-fit: cover;
}
.image-content{
    display: grid;
    justify-items: center;
}
.image-image{
    display: grid;
   grid-template-columns:1fr 1fr;
   margin-bottom: 50px;
    width:80%;
    margin:auto;
}
.image-image img{
   text-align: center;
   width:500px;
   height:500px;
   margin:10px auto;
   object-fit:cover;
}
.image-image .img-one,
.image-image .img-four,
.image-image .img-five{
    width:500px;  
}
.image-image .img-two,
.image-image .img-three,
.image-image .img-six{
    width:450px;
}
.main-contact-container{
    display:grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    text-align: center;
    padding-bottom: 50px;
    background:var(--primary);
} 
  .contact-image img{
    width: 700px;
    height: 600px;
    object-fit: cover;
    margin:auto;
    margin-top:30px;
}
.contact-image-icons{
    background:var(--primary);
    display: grid;
    grid-template-columns: repeat(4,1fr);
    margin:0 10px;
    justify-items: center;
    width:50%;
    margin:50px auto;
}
.contact-image-icons img{
    width:30px;
    height:30px;
}
.contact-content {
    display: block;
    line-height: 40px;
    color:var(--third);
}
.contact-content h1{
    font-size: 80px;
    font-weight:600;
    padding:30px;  
    color:var(--second);
}
.contact-content h3{
    font-size: 17px;
    font-weight:600;  
}
.image-icons{
    display: block;
    position: fixed;
    right:1px;
    top:50%;
    padding:10px 5px;
    background-color: var(--primary);
    border-top-left-radius:10px;
}
.image-icons img,.contact-image-icons img{
    width:30px;
    height:30px;
    display: block;
    margin-bottom:10px;
}
@media(max-width:1007px){
    .image-image{
        display: grid;
       grid-template-columns:1fr 1fr;
       margin-bottom: 50px;
        width:100%;
    }
}
@media(max-width:640px){
    body{
        background:var(--primary);
        overflow-x: hidden;
    }
    .body2{
        overflow-y: hidden;
    }
    .der{
        overflow: hidden;
    }
    .main-header-container{
        background-color: var(--primary);
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
      
    }
    .main-header-container .side-image img,
    .main-header-container .side-image
    { 
        display: none;
    }
    .main-about-container .about-image
    {
        display: block;
    }
    .main-header-container .name-humbergur{
        display: grid;
    }
    .name-humbergur{
        background:var(--primary);
        display:grid;
        grid-template-columns: 80% 10%;
        column-gap: 10%;
        margin:15px;
        margin-right:10px;
    }
    .name-humbergur h1{
        font-size: 20px;
        font-weight: normal;
        color:var(--second);
    }
    .name-humbergur button{
        background:transparent;
        width:30px;
        height:25px;
        display:grid;
        grid-template-columns: 1fr;
        row-gap: 5px;
        border:none;
        outline:none;
        transform: unset;
        position:fixed;
        right:5%;
    }
    .name-humbergur button > span:nth-child(1),
    .name-humbergur button > span:nth-child(2),
    .name-humbergur button > span:nth-child(3){
        transition: 1s; 
    }
    .name-humbergur button span{
        background:var(--second);
    }
    .image-icons{
        position: unset;
        display: grid;
        grid-template-columns: repeat(4,1fr);
        margin:0 10px;
        justify-items: center;
        padding:30px 0;
    }
    .image-icons img,.contact-image-icons img{
        width:30px;
        height:30px;
    }
    .hey-name .links{
       display: none;
    }
    .hey-name .links2{
        display: inline;
         position: absolute;
        width:100%;
        height:100%;
        background: var(--primary);
        margin-top:-170px;
        margin-left:-200px;
        text-align: center;
        line-height: 150px;
        padding-top: 70px;
        opacity: 0.97;
        animation:slider;
        animation-duration: 0.7s;
        animation-fill-mode: forwards;
        position: fixed;
        
    }
   @keyframes slider{
       from{transform: translateX(-700px);}
       to{transform: translateX(0px);}
   }
    .hey-name .links2 a{
        display:block;
        align-self: center;
        font-size: 60px;  
    }
    .hey-name .head-name{
        display:none;
    }
    .name-humbergur .button2{
        z-index:100;
        background:var(--primary);
        width:30px;
        height:25px;
        display:grid;
        grid-template-columns: 1fr;
        row-gap: 5px;
        border:none;
        outline:none;
        
    }
    .name-humbergur .button2 > span:nth-child(2){
        background-color: var(--second);
        transition: 2s;
        transform: translateX(100px);
    }
    .name-humbergur .button2 > span:nth-child(1),
    .name-humbergur .button2 > span:nth-child(3) {
        background-color: var(--second);
        transition: 1s;
        transform-origin: center;
        transform: translateY(10px) rotate(45deg);
    }
    .name-humbergur .button2 > span:nth-child(3){
        transform-origin: center;
        transform: translateY(-10px) rotate(-45deg);
    }
    .hey-name{
        background:var(--primary);
        margin: 25px 10px;
        height:30vh;
        justify-self: center;
    }

   

    .hey-name .hey-h1{
        font-size: 30px;
        font-weight: 500;  
        color:var(--second);
        margin-top:0px;
    }
    .hey-name .name-h1{
        font-size: 30px;
        padding:20px 0px;  
        color:var(--fourth);
    }
   
    .hey-name .hey-p{
        font-size: 17px;
        color:var(--third);
    }
    .main-about-container,
    .main-show-container,
    .main-image-container
    {
        display:grid;
        grid-template-columns: 1fr;
        grid-template-rows: 2fr;
        text-align: center;
        background:var(--fourth);
    }
    .main-about-container{
        width:100%;
        background:var(--fourth);
    }
    .main-about-container .about-content{
    width:100%;
    }
    .about-image img,.show-image img{
        width:100%;
    }
    .about-content,
    .show-content,
    .image-content
    {
        display:block;
    }
    .show-content{
        background:var(--third);
    }
    .show-content h1{
        margin-bottom:0px;
        margin-top:0px;
        font-size: unset;
    }
    .show-image img{
        width: 100%;
        height: 50vh;
    }
    .about-content h1,
    .show-content h1,
    .image-content h1
    {
        font-size: 27px;
        font-weight:600;
        padding:30px;  
        color:var(--primary); 
        margin:0px;  
    }
    .about-content p{
        padding:10px;
        line-height: 30px;
        font-size: 15px;
    }
    .about-content button{
        width:70%;
        margin:auto;
        height:50px;
        margin:40px;
        border:none;
        outline: none;
        background:var(--second);
        color:var(--primary);
    }
    .image-image{
         display: grid;
        grid-template-columns: 1fr;
        margin-bottom: 50px;
        width:unset;
    }
    .image-image img,
    .image-image .img-one,
    .image-image .img-two,
    .image-image .img-three,
    .image-image .img-four,
    .image-image .img-five,
    .image-image .img-six
    {
        text-align: center;
        width:600px;
        height:600px;
        margin:10px auto;
    }
    .main-contact-container{
        display:grid;
        grid-template-columns: 1fr;
        grid-template-rows: 2fr;
        text-align: center;
    } 
      .contact-image img{
        width:90%;
        margin:auto;
        margin-top:30px;
    }
    .contact-image-icons{
        background:var(--primary);
        display: grid;
        grid-template-columns: repeat(4,1fr);
        margin:0 10px;
        justify-items: center;
        margin-top: 40px;
        width:unset;
    }
    .contact-content {
        display: block;
        line-height: 40px;
        color:var(--third);
    }
    .contact-content h1{
        font-size: 27px;
        font-weight:600;
        padding:30px;  
        color:var(--second);
    }
    .contact-content h3{
        font-size: 17px;
        font-weight:600;  
    }


}